import React, { useState } from 'react';
import Typical from 'react-typical';
import Switch from 'react-switch';

const Header = ({ sharedData, resume_download }) => {
    var titles = sharedData.titles.map((title) => [title.toUpperCase(), 1500]).flat();
    const dataThemeAttribute = 'data-theme';
    const [theme, setTheme] = useState(false);
    const handleThemeChange = () => {
        const body = document.body;
        const newTheme = body.getAttribute(dataThemeAttribute) === 'dark' ? 'light' : 'dark';
        body.setAttribute(dataThemeAttribute, newTheme);
        theme ? setTheme(false) : setTheme(true);
    };

    const HeaderTitleTypeAnimation = React.memo(
        () => {
            return <Typical className="title-styles" steps={titles} loop={50} />;
        },
        (props, prevProps) => true
    );

    return (
        <header id="home" style={{ height: window.innerHeight - 140, display: 'block' }}>
            <div className="row aligner" style={{ height: '100%' }}>
                <div className="col-md-12">
                    <div>
                        <span className="iconify header-icon" data-icon="la:laptop-code" data-inline="false"></span>
                        <br />
                        <h1 className="mb-0">
                            <Typical steps={[sharedData.name]} wrapper="p" />
                        </h1>
                        <div className="title-container mb-5">
                            <HeaderTitleTypeAnimation />
                        </div>
                        <Switch
                            checked={theme}
                            onChange={handleThemeChange}
                            offColor="#635bff"
                            onColor="#353535"
                            className="react-switch mx-auto"
                            width={90}
                            height={40}
                            uncheckedIcon={
                                <span
                                    className="iconify"
                                    data-icon="twemoji:owl"
                                    data-inline="false"
                                    style={{
                                        display: 'block',
                                        height: '100%',
                                        fontSize: 25,
                                        textAlign: 'end',
                                        marginLeft: '20px',
                                        color: '#353239'
                                    }}
                                ></span>
                            }
                            checkedIcon={
                                <span
                                    className="iconify"
                                    data-icon="noto-v1:sun-with-face"
                                    data-inline="false"
                                    style={{
                                        display: 'block',
                                        height: '100%',
                                        fontSize: 25,
                                        textAlign: 'end',
                                        marginLeft: '10px',
                                        color: '#353239'
                                    }}
                                ></span>
                            }
                            id="icon-switch"
                        />
                        <p>
                            <a
                                href={resume_download}
                                className="btn mt-5 text-center"
                                download
                                style={{ fontSize: '150%' }}
                            >
                                <i className="fas fa-download"></i> Resume
                            </a>
                        </p>
                        <br></br>
                    </div>
                </div>
            </div>
        </header>
    );
};

export default Header;
