import React from 'react';
import { Modal } from 'react-bootstrap';
import AwesomeSlider from 'react-awesome-slider';
import AwesomeSliderStyles from '../scss/light-slider.scss';
import AwesomeSliderStyles2 from '../scss/dark-slider.scss';
import 'react-awesome-slider/dist/custom-animations/scale-out-animation.css';

const ProjectDetailsModal = ({ show, data, onHide }) => {
    const { title, technologies, images, description, url } = data;

    const Technologies = technologies.map((icon, i) => {
        const style = icon.style ? { color: 'red', fontSize: '300%' } : { fontSize: '300%' };
        return (
            <li className="list-inline-item mx-3" key={i}>
                {icon.class === 'iconify' ? (
                    <>
                        <span className={icon.class} data-icon={icon.dataIcon} style={style}></span>
                        <div className="text-center">
                            <p className="text-center" style={{ fontSize: '75%' }}>
                                {icon.name}
                            </p>
                        </div>
                    </>
                ) : (
                    <span>
                        <div className="text-center">
                            <i className={icon.class} style={{ fontSize: '300%' }}>
                                <p className="text-center" style={{ fontSize: '30%' }}>
                                    {icon.name}
                                </p>
                            </i>
                        </div>
                    </span>
                )}
            </li>
        );
    });

    const Images = images.map((image, i) => {
        return <div key={i} data-src={image} />;
        // return <img style={{ transform: 'scale(1)' }} key={i} src={image} />;
    });
    return (
        <Modal
            show={show}
            onHide={onHide}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
            className="modal-inside"
        >
            <span onClick={onHide} className="modal-close">
                <i className="fas fa-times fa-3x close-icon"></i>
            </span>
            <div className="col-md-12">
                <div className="col-md-10 mx-auto" style={{ paddingBottom: '50px' }}>
                    <div className="slider-tab">
                        <span
                            className="iconify slider-iconfiy"
                            data-icon="emojione:red-circle"
                            data-inline="false"
                            style={{ marginLeft: '5px' }}
                        ></span>{' '}
                        &nbsp;{' '}
                        <span
                            className="iconify slider-iconfiy"
                            data-icon="twemoji:yellow-circle"
                            data-inline="false"
                        ></span>{' '}
                        &nbsp;{' '}
                        <span
                            className="iconify slider-iconfiy"
                            data-icon="twemoji:green-circle"
                            data-inline="false"
                        ></span>
                    </div>
                    <AwesomeSlider
                        cssModule={[AwesomeSliderStyles, AwesomeSliderStyles2]}
                        animation="scaleOutAnimation"
                        className="slider-image"
                    >
                        {Images}
                    </AwesomeSlider>
                </div>
                <div className="col-md-10 mx-auto">
                    <h3 style={{ padding: '5px 5px 0 5px' }}>
                        {title}
                        {url ? (
                            <a href={url} target="_blank" rel="noopener noreferrer" className="link-href">
                                <i className="fas fa-external-link-alt" style={{ marginLeft: '10px' }}></i>
                            </a>
                        ) : null}
                    </h3>
                    <p className="modal-description">{description}</p>
                    <div className="col-md-12 text-center">
                        <ul className="list-inline mx-auto">{Technologies}</ul>
                    </div>
                </div>
            </div>
        </Modal>
    );
};

export default ProjectDetailsModal;
